import { Component } from '@angular/core';
import {NavController, ToastController} from 'ionic-angular';
import {Headers, Http} from '@angular/http';
import 'rxjs/add/operator/map';
import { AuthService } from '../../providers/auth-service';
import {VotingService} from "../../providers/voting-service";
import {VotingsPage} from "../votings/votings";
import {Storage} from "@ionic/storage";

@Component({
  selector: 'page-vote',
  templateUrl: 'vote.html',
})
export class VotePage {

    rangeValueMood = 3;
    valueReason = "";
    loaded: boolean = false;

    votegroupdata: any;            //Liste aller aktuellen Abstimmungen
    info: any;                      //Aktuelle Nutzerdaten
    votingInfo: any = null;
    body: any;
    hostaddress: any;

    constructor( private nav: NavController, public http: Http, public toastCtrl: ToastController, private auth: AuthService, private voting: VotingService, private storage: Storage) {

      this.info = this.auth.getUserInfo();    //Lade den aktuellen Nutzer
      this.votingInfo = this.voting.getVotingInfo();
      console.log(this.votingInfo.votingId);

      /** Hostadresse aus dem Storage holen */
      storage.get('hostaddress').then((val) => {
        this.hostaddress = val;
        this.loaded = true;
      });
    }

    public vote()
    {

      let headers = new Headers();
      headers.append('Authorization', "Bearer " + this.info.jwt);
      headers.append('Content-Type', 'application/json');

      this.body = "{\"mood\":"+this.rangeValueMood+", \"reason\":\""+this.valueReason+"\", \"votegroupId\": "+ this.votingInfo.votingId+"}";
      console.log(this.body);

      this.http.post(this.hostaddress+'vote', this.body, { headers: headers}).map(res => res.text()).subscribe(
        data =>
        {
          console.log(data.toString());
          this.votegroupdata = data;
          this.SuccessMsg();
          this.nav.setRoot(VotingsPage);
        },
        err =>
        {
          console.log(err.toString());
        });
    }

  SuccessMsg() {
    let toast = this.toastCtrl.create({
      message: `Stimme erfolgreich abgegeben!`,
      duration: 1500,
      position: 'bottom'
    });
    toast.present();
  }
  /*
  //Bei fehlerhaften Laden eine definierte Nachricht ausgeben
  ErrorMsg(content) {
    let toast = this.toastCtrl.create({
      message: content,
      duration: 1200,
      position: 'bottom'
    });
    toast.present();
  }
    //Auth Nutzer zur�cksetzen Testzwecke
      /**
    public logout(){
        this.auth.logout().subscribe(succ => {
        });
    }**/
}



