import { Injectable } from '@angular/core';
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/operator/map';

/**
 * voting-service verwaltet die aktuell ausgewählte Abstimmung
 * **/

export class votingdata {
  votingId: any;
  name:any;
  category: any;
  creator: any;
  startdate: any;
  enddate: any;

  constructor(votingId: any, name:any, category:any, creator:any, startdate:any, enddate: any) {
    this.votingId = votingId;
    this.name = name;
    this.category = category;
    this.creator = creator;
    this.startdate = startdate;
    this.enddate = enddate;
  }
}

@Injectable()
export class VotingService {
  currentVotingdata: votingdata;

  //Erstellen des aktuellen Nutzer
  public create(votingId, name, category, creator, startdate, enddate) {

    return Observable.create(observer => {
      this.currentVotingdata = new votingdata(votingId, name, category, creator, startdate, enddate);
      observer.next(true);
      observer.complete();
    });
  }

  //Aktuellen Nutzer zurückgeben
  public getVotingInfo() : votingdata {
    return this.currentVotingdata;
  }

  public setNull() {
    return Observable.create(observer => {
      this.currentVotingdata = null;
      observer.next(true);
      observer.complete();
    });
  }
}



